<template>
  <div class="info-notice-add">
    <el-form :model="noticeData" :rules="rules" ref="noticeData" label-width="100px" class="user-form">
      <el-form-item label="公告标题" prop="title">
        <el-input v-model="noticeData.title" size="small" :disabled="!notDetial" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="来源" prop="source">
        <el-input v-model="noticeData.source" size="small" :disabled="!notDetial" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="公告內容" prop="content">
          <vue-editor v-model="noticeData.content" :disabled="!notDetial"  useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
      </el-form-item>
      <el-form-item label="公告附件" :disabled="!notDetial">
          <el-upload :action="uploadAction"
            :before-upload="beforeAvatarUpload"
            :limit="10"
            name="resFile"
            :data="fileName"
            :accept="acceptList"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            :file-list="fileList"
            :on-remove="delFile">
            <el-button size="small" plain :disabled="uploadState">点击上传</el-button>
          </el-upload>
      </el-form-item>
    </el-form>
    <div class="form-submit">
      <el-button v-if="notDetial" type="primary" round size="small" @click="saveForm(1)">保存</el-button>
      <el-button v-if="notDetial" type="warning" round size="small" @click="saveForm(2)">保存并申请发布</el-button>
      <el-button type="primary" round plain size="small" @click="toNoticeList">{{cancelTxt}}</el-button>
    </div>
  </div>

</template>
<script>
import axios from 'axios'
import { informationNotice as api } from '../../api'
import { VueEditor } from 'vue2-editor'
export default {
  data() {
    return {
      uploadState: false,
      cancelTxt: '取消',
      limit: 1,
      notDetial: true,
      noticeData: {
        status: 1,
        title: '',
        source: '',
        content: '',
        attachmentName: '',
        attachmentUrl: ''
      },
      uploadParams: {
        filename: '',
        type: 3
      },
      acceptList: 'application/pdf,application/vnd.ms-excel,application/msword,application/vnd.ms-works',
      uploadAction: window.api_upload,
      fileName: {
        filename: ''
      },
      fileList: [],
      rules: {
        title: [{ required: true, message: '请输入公告标题', trigger: 'blur' }],
        source: [{ required: false, message: '请輸入公告', trigger: 'change' }],
        content: [
          { required: true, message: '请輸入公告內容', trigger: 'change' }
        ]
      }
    }
  },
  components: {
    VueEditor
  },
  methods: {
    beforeAvatarUpload(file) {
      if (file.type !== 'application/pdf' && file.type !== 'application/vnd.ms-excel' && file.type !== 'application/msword' && file.type !== 'application/vnd.ms-works') {
        this.fileList.unshift({name: this.fileName.filename, url: ''})
        this.$msgbox({
          title: '系统提示',
          message: '<i class="iconfont icon-tanhao"></i><span>请上 pdf,doc,xls,wps文件</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {}
        })
        return false
      }
      if (file.size > 31457280) {
        this.fileList.unshift({name: this.fileName.filename, url: ''})
        this.$msgbox({
          title: '系统提示',
          message: '<i class="iconfont icon-cuowu"></i><span>上传文件不得大于2M</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {}
        })
        return false
      }
      this.fileName.filename = file.name
      this.uploadState = true
    },
    uploadSuccess(res, file, List) {
      let that = this
      let respone = res.data
      for (var i = 0; i < respone.length; i++) {
        that.fileList.push({name: that.fileName.filename, url: respone[i].resOriginalUrl})
      }
      this.uploadState = false
    },
    uploadError() {
      this.$msgbox({
        title: '系统提示',
        message: '<i class="iconfont icon-cuowu"></i><span>上传失败</span>',
        center: true,
        roundButton: true,
        customClass: 'messageBox',
        type: 'success',
        dangerouslyUseHTMLString: true,
        callback: () => {}
      })
    },
    delFile(file, list) {
      this.fileList.splice(list, 1)
    },
    saveForm(status) {
      let that = this
      this.$refs['noticeData'].validate(valid => {
        if (valid) {
          let noticeData = that.noticeData
          noticeData.status = 1
          if (status === 2) {
            noticeData.status = 2
          }
          noticeData.attachmentName = JSON.stringify(this.fileList)
          if (this.$route.query.isEdit) {
            // 修改公告
            this.noticeUpdate(noticeData)
          } else {
            // 新增公告
            this.noticeAdd(noticeData)
          }
        } else {
          return false
        }
      })
    },
    // 新增公告
    noticeAdd(noticeData) {
      api.noticeAdd(noticeData).then(res => {
        this.$message(
          {
            message: '通知公告添加成功',
            type: 'success'
          },
          2000
        )
        this.noticeData = {
          status: 1,
          title: '',
          source: '',
          content: '',
          attachmentName: '',
          attachmentUrl: ''
        }
        this.$router.push('/information/infoNotice')
      })
    },
    // 查询公告详情
    noticeDetail(id) {
      api.noticeDetail({ id: id }).then(res => {
        console.log(res)
        this.noticeData.title = res.data.title
        this.noticeData.source = res.data.source
        this.noticeData.content = res.data.content
        res.data.alist.map(value => {
          this.fileList.push({
            name: value.attachmentName,
            url: value.attachmentUrl
          })
        })
      })
    },
    // 修改公告
    noticeUpdate(noticeData) {
      noticeData.id = this.$route.query.id
      api.noticeUpdate(noticeData).then(res => {
        this.$message(
          {
            message: '通知公告修改成功',
            type: 'success'
          },
          2000
        )
        this.noticeData = {
          status: 1,
          title: '',
          source: '',
          content: '',
          attachmentName: '',
          attachmentUrl: ''
        }
        this.$router.push('/information/infoNotice')
      })
    },
    toNoticeList() {
      this.$router.push('/information/infoNotice')
    },
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      if (!this.notDetial) {
        return false
      }
      let that = this
      if (file.type !== 'image/png' && file.type !== 'image/jpeg' && file.type !== 'image/bmp') {
        this.$msgbox({
          title: '系统提示',
          message: '<i class="iconfont icon-tanhao"></i><span>请上传图片</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {}
        })
        return false
      }
      if (file.size > 2097152) {
        this.$msgbox({
          title: '系统提示',
          message: '<i class="iconfont icon-tanhao"></i><span>上传文件不得大于2M</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {}
        })
        return false
      }
      var formData = new FormData()
      formData.append('resFile', file)
      formData.append('filename', file.name)
      axios({
        url: that.uploadAction,
        method: 'POST',
        data: formData
      }).then((result) => {
        let url = result.data.data[0].resOriginalUrl
        Editor.insertEmbed(cursorLocation, 'image', url)
        resetUploader()
      }).catch((err) => {
        console.log(err)
      })
    }
  },
  created() {
    this.uploadAction = window.api_upload
  },
  beforeMount() {
    if (this.$route.query.isDetial) {
      this.notDetial = false
    }
  },
  mounted() {
    console.log(this.$route.query)
    if (this.$route.query.isEdit) {
      this.noticeDetail(this.$route.query.id)
    }
    if (this.$route.query.isDetial) {
      this.cancelTxt = '返回'
      this.noticeDetail(this.$route.query.id)
    }
  },
  watch: {
    fileList(value) {
      console.log(value)
    }
  }
}
</script>
<style lang="scss" scoped>
.info-notice-add {
  padding: 30px;
  box-sizing: border-box;
  .user-form {
    width: 600px;
    margin: auto;
    .el-input {
      width: 60%;
      margin-right: 5px;
    }
  }
  .form-submit {
    text-align: center;
    margin-top: 80px;
  }
  .ql-snow .ql-toolbar button svg,
  .quillWrapper .ql-snow.ql-toolbar button svg {
    width: 14px;
    height: 14px;
  }
  & /deep/  [disabled], input[disabled] {
   opacity: 1.0; 
   color:#ccc
  }
  & /deep/ .el-input.is-disabled .el-input__inner{
    color:#8a8d94
  }
  & /deep/ .ql-editor{
    strong{
      font-weight: bolder;
      em{
        font-style: italic;
        font-weight: bolder;
      }
    }
    em{
      font-style: italic;
    }
  }
}
</style>
<style lang="scss">
@import '~@/assets/css/theme.scss';
.info-policy-add {
  .el-form-item.is-required .el-form-item__label:before {
    content: '';
    color: #f56c6c;
    margin-right: 4px;
  }
  .el-form-item.is-required .el-input:after {
    content: '*';
    color: #f56c6c;
    margin-right: 4px;
    position: absolute;
    right: -20px;
  }
}
</style>



