<template>
  <div class="info-policy-add">
    <el-form :model="policyData" :rules="rules" ref="policyData" label-width="100px" class="user-form">
      <el-form-item label="政策名称" prop="policyName">
        <el-input v-model="policyData.policyName" size="small" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="政策类型" prop="policyType">
        <el-select  v-model="policyData.policyType" placeholder="请选择" size="small" style="width:60%">
           <el-option
            v-for="item in policyTypeDef"
            :key="item.id"
            :label="item.policyTypeName"
            :value="item.policyTypeName">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="政策级别" prop="policyLevel">
        <el-select v-model="policyData.policyLevel" placeholder="请选择" size="small" style="width:60%">
          <el-option
            v-for="item in policyLevelDef"
            :key="item.id"
            :label="item.policyLevelName"
            :value="item.policyLevelName">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="releasetime" label="发布日期" >
          <el-date-picker type="datetime"  v-model="policyData.releasetime"  value-format="yyyy-MM-dd HH:mm:ss"  @change="getETime"  placeholder="发布日期" size="small"></el-date-picker>
        </el-form-item>
      <el-form-item label="正文" class="bold">
      </el-form-item>
      <el-form-item label="政策依据">
          <vue-editor v-model="policyData.policyAccording" id="policyAccording" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
      </el-form-item>
      <el-form-item label="政策要点">
          <vue-editor v-model="policyData.policyPointe" id="policyPointe" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
      </el-form-item>
      <el-form-item label="适用对象">
          <vue-editor v-model="policyData.suitableUsers" id="suitableUsers" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
      </el-form-item>
      <el-form-item label="申报条件">
          <vue-editor v-model="policyData.qualification" id="qualification" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
      </el-form-item>
      <el-form-item label="提交材料">
          <vue-editor v-model="policyData.submission" id="submission" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
      </el-form-item>
      <el-form-item label="办理流程">
          <vue-editor v-model="policyData.handlingProcedures" id="handlingProcedures" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
      </el-form-item>
      <el-form-item label="政策附件">
          <el-upload :action="uploadAction"
            :before-upload="beforeAvatarUpload"
            :limit="10"
            name="resFile"
            :data="fileName"
            :accept="acceptList"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            :file-list="fileList"
            :on-remove="delFile">
            <el-button size="small" plain :disabled="uploadState">点击上传</el-button>
          </el-upload>
      </el-form-item>
    </el-form>
    <div class="form-submit">
      <el-button type="primary" round size="small" @click="save">保存</el-button>
      <el-button type="warning" round size="small" @click="saveExl">保存并申请发布</el-button>
      <el-button type="primary" round plain size="small" @click="back">取消</el-button>
    </div>
  </div>

</template>
<script>
  import axios from 'axios'
  import {information as api} from '../../api'
  import { VueEditor } from 'vue2-editor'
  export default {
    data() {
      return {
        uploadState: false,
        policyData: {
          policyName: '',
          policyType: '',
          policyTypeId: '',
          policyLevel: '',
          policyLevelId: '',
          status: null,
          releasetime: '',
          policyAccording: '政策依据',
          policyPointe: '政策要点',
          suitableUsers: '使用对象',
          qualification: '申报条件',
          submission: '提交材料',
          handlingProcedures: '办理流程',
          policyAttachmentsName: '',
          policyAttachmentsUrl: ''
        },
        uploadAction: window.api_upload,
        fileName: {
          filename: ''
        },
        policyTypeDef: [],
        policyLevelDef: [],
        acceptList: 'application/pdf,application/vnd.ms-excel,application/msword,application/vnd.ms-works',
        fileList: [],
        rules: {
          policyName: [
            { required: true, message: '请输入政策名称', trigger: 'blur' }
          ],
          policyType: [
            { required: true, message: '请选择政策类型', trigger: 'change' }
          ],
          policyLevel: [
            { required: true, message: '请选择政策级别', trigger: 'change' }
          ],
          releasetime: [
            { required: true, message: '请选择发布时间', trigger: 'change' }
          ]
        }
      }
    },
    components: {
      VueEditor
    },
    methods: {
      back() {
        this.$router.back()
      },
      beforeAvatarUpload(file) {
        if (file.type !== 'application/pdf' && file.type !== 'application/vnd.ms-excel' && file.type !== 'application/msword' && file.type !== 'application/vnd.ms-works') {
          this.fileList.unshift({name: this.fileName.filename, url: ''})
          this.$msgbox({
            title: '系统提示',
            message: '<i class="iconfont icon-tanhao"></i><span>请上 pdf,doc,xls,wps文件</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {}
          })
          return false
        }
        if (file.size > 31457280) {
          this.fileList.unshift({name: this.fileName.filename, url: ''})
          this.$msgbox({
            title: '系统提示',
            message: '<i class="iconfont icon-cuowu"></i><span>上传文件不得大于30M</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {}
          })
          return false
        }
        this.fileName.filename = file.name
        this.uploadState = true
      },
      uploadSuccess(res, file, List) {
        let that = this
        let respone = res.data
        for (var i = 0; i < respone.length; i++) {
          that.fileList.push({name: that.fileName.filename, url: respone[i].resOriginalUrl})
        }
        this.uploadState = false
      },
      uploadError() {
        this.$msgbox({
          title: '系统提示',
          message: '<i class="iconfont icon-cuowu"></i><span>上传失败</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {}
        })
      },
      delFile(file, list) {
        this.fileList.splice(list, 1)
      },
      getETime(val) {
        this.policyData.releasetime = val
      },
      handleImageAdded(file, Editor, cursorLocation, resetUploader) {
        let that = this
        if (file.type !== 'image/png' && file.type !== 'image/jpeg' && file.type !== 'image/bmp') {
          this.$msgbox({
            title: '系统提示',
            message: '<i class="iconfont icon-tanhao"></i><span>请上传图片</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {}
          })
          return false
        }
        if (file.size > 2097152) {
          this.$msgbox({
            title: '系统提示',
            message: '<i class="iconfont icon-tanhao"></i><span>上传文件不得大于2M</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {}
          })
          return false
        }
        var formData = new FormData()
        formData.append('resFile', file)
        formData.append('filename', file.name)
        axios({
          url: that.uploadAction,
          method: 'POST',
          data: formData
        }).then((result) => {
          let url = result.data.data[0].resOriginalUrl
          Editor.insertEmbed(cursorLocation, 'image', url)
          resetUploader()
        }).catch((err) => {
          console.log(err)
        })
      },
      saveForm() {
        console.log(this.policyData.policyAccording)
        let that = this
        that.$refs['policyData'].validate((valid) => {
          if (valid) {
            let policyData = that.policyData
            policyData.policyAttachmentsName = JSON.stringify(that.fileList)
            api.policyInsert(policyData).then(res => {
              if (res.code === 1) {
                that.$msgbox({
                  title: '系统提示',
                  message: '<i class="iconfont icon-correct"></i><span>' + res.msg + '</span>',
                  center: true,
                  roundButton: true,
                  customClass: 'messageBox',
                  type: 'success',
                  dangerouslyUseHTMLString: true,
                  callback: () => {
                    that.back()
                  }
                })
              }
            }).catch(err => {
              this.$msgbox({
                title: '系统提示',
                message: '<i class="iconfont icon-cuowu"></i><span>' + err.msg + '</span>',
                center: true,
                roundButton: true,
                customClass: 'messageBox',
                type: 'success',
                dangerouslyUseHTMLString: true,
                callback: () => {}
              })
            })
          } else {
            return false
          }
        })
      },
      save() {
        this.policyData.status = 1
        this.saveForm()
      },
      saveExl() {
        this.policyData.status = 2
        this.saveForm()
      }
    },
    mounted() {
      let that = this
      api.policyLevel('').then(res => {
        that.policyLevelDef = res.data.list
      })
      api.policyType('').then(res => {
        that.policyTypeDef = res.data.list
      })
    }
  }
</script>
<style lang="scss" scoped>
  .info-policy-add{
    padding:30px;
    box-sizing:border-box;
    .user-form{
      width:600px;
      margin: auto;
      .el-input{
        width:60%;
        margin-right: 5px;
      }
    }
    .form-submit{
      text-align: center;
      margin-top:80px;
    }
    .ql-snow .ql-toolbar button svg, .quillWrapper .ql-snow.ql-toolbar button svg{
        width:14px;
        height: 14px
      }
    & /deep/ .el-form-item.is-required .el-form-item__label:before{
        content: '';
        color: #f56c6c;
        margin-right: 4px;
      }
      & /deep/ .el-form-item.is-required .el-input:after{
        content: '*';
        color: #f56c6c;
        margin-right: 4px;
        position: absolute;
        right:-20px;
      }
      & /deep/ .ql-editor{
        strong{
          font-weight: bolder;
          em{
            font-style: italic;
            font-weight: bolder;
          }
        }
        em{
          font-style: italic;
        }
      }
  }
</style>


