<template>
  <div class="info-policy j-theme j-scient">
	<el-form :inline="true" label-width="80px">
	  <el-form-item label="标题" >
	    <el-input placeholder="标题" v-model="checkCondition.title" size="small" style="width: 196px" maxlength="100"></el-input>
	  </el-form-item>
	  <el-form-item label="状态">
	   <el-select v-model="checkCondition.status" placeholder="请选择"  size="small"  style="width: 196px">
	    <el-option
	      v-for="item in options"
	      :key="item.id"
	      :label="item.policyStatuName"
	      :value="item.id">
	    </el-option>
	  </el-select>
	  </el-form-item>
     <el-form-item label="新增时间" class="icon-no-input-padding">
      <el-col :span="11">
        <el-date-picker
          type="date" placeholder="选择日期"
          style="width: 108px"
          v-model="checkCondition.newtimeStart"
          size="small"
          value-format="yyyy-MM-dd HH:mm:ss"
        ></el-date-picker>
        <!-- <el-date-picker v-model="checkCondition.newtimeStart" type="date" placeholder="选择日期" value-format='yyyy-mm-dd HH:mm:ss' style="width: 100px"  size="small"></el-date-picker> -->
      </el-col>
      <el-col class="line" :span="2">-</el-col>
      <el-col :span="11">
        <el-date-picker
          type="date" placeholder="选择日期"
          style="width: 108px"
          v-model="checkCondition.newtimeEnd"
          size="small"
          value-format="yyyy-MM-dd HH:mm:ss"
        ></el-date-picker>
      </el-col>
    </el-form-item>
    <el-form-item label="发布时间" class="icon-no-input-padding">
      <el-col :span="11">
        <el-date-picker
          type="date" placeholder="选择日期"
          style="width: 108px"
          v-model="checkCondition.releasetimeStart"
          size="small"
          value-format="yyyy-MM-dd HH:mm:ss"
        ></el-date-picker>
      </el-col>
      <el-col class="line" :span="2">-</el-col>
      <el-col :span="11">
        <el-date-picker
          type="date" placeholder="选择日期"
          style="width: 108px"
          v-model="checkCondition.releasetimeEnd"
          size="small"
          value-format="yyyy-MM-dd HH:mm:ss"
        ></el-date-picker>
      </el-col>
    </el-form-item>
    <el-form-item label="浏览量">
      <el-col :span="11">
        <el-input placeholder="" type="number" min='0' v-model="checkCondition.pageviewStart" size="small" style="width: 90px;" @change="psva"></el-input>
      </el-col>
       <el-col class="line" :span="2">-</el-col>
       <el-col :span="11">
        <el-input placeholder="" type="number" min='0' v-model="checkCondition.pageviewEnd" size="small" style="width: 90px;"  @change="pnva"></el-input>
      </el-col>
    </el-form-item>
	</el-form>
  <div class="form-submit">
      <el-button type="primary" size="small" round @click="check" v-auth="AUTH.TechSelect">查询</el-button>
      <el-button type="primary" size="small" round plain @click="reset" v-auth="AUTH.TechSelect">重置</el-button>
      <el-button type="warning" size="small" round @click="add()" v-auth="AUTH.TechInsert">新增</el-button>
  </div>
  <el-table :data="tableData" stripe border style="width: 100%" class="en-opeartion">
      <el-table-column prop="title" label="标题" show-overflow-tooltip width="220" align="left"></el-table-column>
      <el-table-column prop="status" label="状态" show-overflow-tooltip>
         <template slot-scope="scope"><span :class="scope.row.status | statusClass">{{scope.row.status | statusTypeName}}</span></template>
      </el-table-column>
      <el-table-column prop="newtime" align="center" label="新增时间" width="170">
        <template slot-scope="scope">{{scope.row.newtime | dateFormat}}</template>
      </el-table-column>
      <el-table-column prop="releasetime" align="center" label="发布时间" width="170">
        <template slot-scope="scope">{{scope.row.releasetime | dateFormat}}</template>
      </el-table-column>
      <el-table-column prop="pageview" align="center" label="浏览量" width="90"> </el-table-column>
      <el-table-column  align="center" label="操作" width="240">
         <template slot-scope="scope">
           <a class="op-a" v-if="scope.row.status === 1&&optAuth.updateStatus" @click="upload(scope.row)">申请发布</a>
          <span v-if="scope.row.status === 2&&optAuth.updateStatus">
            <a class="op-a" @click="adopt(scope.row)">通过</a>
            <a class="op-a" @click="Unadopt(scope.row)">不通过</a>
          </span>
          <a class="op-a" v-if="scope.row.status === 3&&optAuth.updateStatus" @click="withdraw(scope.row)">撤回</a>
          <a class="op-a" v-if="scope.row.status === 4&&optAuth.updateStatus" @click="upload(scope.row, 2)">申请发布</a>
          <a class="op-a" v-if="scope.row.status === 5&&optAuth.updateStatus" @click="upload(scope.row, 2)">重新发布</a>
          <router-link class="op-a" :to="{name:'scienceTrendAdd',query:{id:scope.row.id}}" v-auth="AUTH.TechUpdate">修改</router-link>
          <router-link class="op-a" :to="{name:'scienceTrendDetial',query:{id:scope.row.id}}" v-auth="AUTH.TechDetail">查看</router-link>
          <a class="op-a" @click="deleteNotice(scope.row.id)" v-auth="AUTH.TechDelete">删除</a>
        </template>
        </el-table-column>
    </el-table>
    <div class="pagination clearFix">
      <div class="pagination-num">
        共<span class="yellow"> {{page.total}} </span>条
      </div>
      <div class="pagination-nav">
         <j-paging class="paging" :initPageSize="page.pageSize" :initPageIndex="page.pageNum" :initTotalCount="page.total" @pageChanged="pageChanged"></j-paging>
      </div>
    </div>
  </div>
</template>
<script>
import { jPaging } from '../../components'
import http from '../../assets/js/request'
import { ButtonAuths } from '../../assets/js/buttonAuth'
import { hasAuth } from '../../assets/js/auth'
export default {
  data() {
    return {
      AUTH: ButtonAuths,
      page: {
        pageSize: 10,
        pageNum: 1,
        total: 94
      },
      checkCondition: {
        title: '',
        status: 0,
        newtimeStart: '',
        newtimeEnd: '',
        releasetimeStart: '',
        releasetimeEnd: '',
        pageviewStart: '',
        pageviewEnd: ''
      },
      options: [],
      tableData: []
    }
  },
  computed: {
    optAuth() {
      // 权限依次是:1、不通过、通过、撤回、申请发布、重新发布;2、修改;3、删除;4、查看
      return {
        updateStatus: hasAuth(this.AUTH.NoticeUpdateStatus),
        update: hasAuth(this.AUTH.NoticeUpdate),
        delete: hasAuth(this.AUTH.NoticeDelete),
        detail: hasAuth(this.AUTH.NoticeDetail)
      }
    }
  },
  methods: {
    psva(value) {
      if (value.length > 5) value = value.slice(0, 5)
      if (value < 0) {
        this.checkCondition.pageviewStart = 0
      } else {
        this.checkCondition.pageviewStart = Math.floor(value)
      }
    },
    pnva(value) {
      if (value.length > 5) value = value.slice(0, 5)
      if (value < 0) {
        this.checkCondition.pageviewEnd = 0
      } else {
        this.checkCondition.pageviewEnd = Math.floor(value)
      }
    },
    isInteger(val) {
      if (val === 'start') {
        if (!Number.isInteger(+this.checkCondition.pageviewStart)) {
          this.checkCondition.pageviewStart = ''
        }
      } else {
        if (!Number.isInteger(+this.checkCondition.pageviewEnd)) {
          this.checkCondition.pageviewEnd = ''
        }
      }
    },
    dateFormat(val) {
      if (!val) return '--'
      let date = new Date(val)
      return (
        date.getFullYear() +
        '-' +
        (date.getMonth() + 1).toString().padStart(2, '0') +
        '-' +
        date
          .getDate()
          .toString()
          .padStart(2, '0') +
        ' ' +
        date
          .getHours()
          .toString()
          .padStart(2, '0') +
        ':' +
        date
          .getMinutes()
          .toString()
          .padStart(2, '0') +
        ':' +
        date
          .getSeconds()
          .toString()
          .padStart(2, '0')
      )
    },
    compareDate(d1, d2) {
      return new Date(d1.replace(/-/g, '/')) > new Date(d2.replace(/-/g, '/'))
    },
    check() {
      if (this.checkCondition.pageviewEnd < this.checkCondition.pageviewStart) {
        this.$message('浏览量输入格式不正确')
        return
      }
      if (this.checkCondition.newtimeStart && this.checkCondition.newtimeEnd) {
        this.checkCondition.newtimeEnd = this.checkCondition.newtimeEnd.replace(
          /00:00:00/g,
          '23:00:00'
        )
        let f = this.compareDate(
          this.checkCondition.newtimeStart,
          this.checkCondition.newtimeEnd
        )
        if (f) {
          this.$message('新增结束时间小于开始时间')
          return
        }
      }
      if (
        this.checkCondition.releasetimeStart &&
        this.checkCondition.releasetimeEnd
      ) {
        this.checkCondition.releasetimeEnd = this.checkCondition.releasetimeEnd.replace(
          /00:00:00/g,
          '23:00:00'
        )
        let f = this.compareDate(
          this.checkCondition.releasetimeStart,
          this.checkCondition.releasetimeEnd
        )
        if (f) {
          this.$message('发布结束时间小于开始时间')
          return
        }
      }
      let token = localStorage.getItem('accessToken')
      let params = { ...this.page, ...this.checkCondition }
      if (this.checkCondition.status === 0) {
        delete params.status
      }
      params.Authorization = token
      // console.log('params', params)
      let _this = this
      http
        .post('/technicalTendency/selectList', params)
        .then(function(res) {
          // for (let item of res.data.list) {
          //   item.newtime = _this.dateFormat(item.newtime)
          //   item.releasetime = _this.dateFormat(item.releasetime)
          // }
          _this.tableData = res.data.list
          _this.page.total = res.data.total
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    updateStatus(status, id, msg) {
      let token = localStorage.getItem('accessToken')
      let params = { status: status, id: id, Authorization: token }
      // let _this = this
      http
        .post('/technicalTendency/updateStatus', params)
        .then(function(res) {
          if (res.code === 1) {
            // _this.$message(
            //   {
            //     message: msg,
            //     type: 'success'
            //   },
            //   1500
            // )
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    deleteNotice(val) {
      this.$confirm('删除数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        center: true,
        roundButton: true,
        customClass: 'messageBox',
        type: 'success'
      })
        .then(() => {
          let params = { id: val }
          let _this = this
          http.post('/technicalTendency/delete', params).then(function(res) {
            if (res.code === 1) {
              _this.$message(
                {
                  message: '删除成功',
                  type: 'success'
                },
                1500
              )
              if (_this.tableData.length === 1) {
                _this.queryParams.pageNum -= 1
              }
              _this.check()
            }
          })
        })
        .catch(() => {
          this.$message(
            {
              type: 'info',
              message: '已取消删除'
            },
            1500
          )
        })
    },
    del(val) {
      let params = { id: val }
      let _this = this
      http
        .post('/technicalTendency/delete', params)
        .then(function(res) {
          if (res.code === 1) {
            _this.$message(
              {
                message: '删除成功',
                type: 'success'
              },
              1500
            )
            _this.check()
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    reset() {
      this.checkCondition = {
        title: '',
        status: 0,
        newtimeStart: '',
        newtimeEnd: '',
        releasetimeStart: '',
        releasetimeEnd: '',
        count: ''
      }
      this.check()
    },
    add() {
      this.$router.push({ name: 'scienceTrendAdd' })
    },
    upload(item) {
      // 发布
      item.status = 2
      this.updateStatus(item.status, item.id, '发布成功')
    },
    adopt(item) {
      // 通过
      item.status = 3
      this.updateStatus(item.status, item.id, '审核成功')
    },
    Unadopt(item) {
      item.status = 4
      this.updateStatus(item.status, item.id, '未审核成功')
    },
    withdraw(item) {
      // 撤销
      item.status = 5
      this.updateStatus(item.status, item.id, '成功撤回')
    },
    pageChanged(val) {
      this.page.pageNum = val
      this.check()
    },
    initStatus() {
      let token = localStorage.getItem('accessToken')
      let params = { Authorization: token }
      let _this = this
      http
        .post('/technicalTendency/showStatue', params)
        .then(function(res) {
          _this.options = res.data
          _this.options.push({ id: 0, policyStatuName: '全部' })
        })
        .catch(function(err) {
          console.log(err)
        })
    }
  },
  watch: {
    // 'checkCondition.pageviewStart'(oldVal, newVal) {
    //   console.log('newVal', Number.isInteger(newVal))
    //   if (!Number.isInteger(newVal)) {
    //     this.$message('浏览量是非正整数')
    //   }
    // },
    // 'checkCondition.pageviewEnd'(oldVal, newVal) {
    //   if (!Number.isInteger(newVal)) {
    //     this.$message('浏览量是非正整数')
    //   } else if (newVal - this.checkCondition.pageviewStart) {
    //     this.$message('浏览量输入不正确')
    //   }
    // }
  },
  mounted() {
    this.check()
    this.initStatus()
  },
  filters: {
    dateFormat2(val) {
      // console.log('val', val)
      if (!val) {
        return ''
      }
      if (val === '--') {
        return ''
      }
      let date = new Date(val)
      return (
        date.getFullYear() +
        '-' +
        (date.getMonth() + 1).toString().padStart(2, '0') +
        '-' +
        date
          .getDate()
          .toString()
          .padStart(2, '0')
      )
    },
    statusTypeName(val) {
      if (val === 0) {
        return '全部'
      } else if (val === 1) {
        return '未发布'
      } else if (val === 2) {
        return '待审核'
      } else if (val === 3) {
        return '已发布'
      } else if (val === 4) {
        return '未通过'
      } else if (val === 5) {
        return '已撤回'
      }
    },
    statusClass(val) {
      if (val === 0) {
        return ''
      } else if (val === 1) {
        return 'color1'
      } else if (val === 2) {
        return 'color2'
      } else if (val === 3) {
        return 'color3'
      } else if (val === 4) {
        return 'color4'
      } else if (val === 5) {
        return 'color5'
      }
    }
  },
  components: {
    jPaging
  }
}
</script>
<style lang="scss" scoped>
.op-a {
  cursor: pointer;
  text-decoration: underline;
  color: #1559ec;
  padding: 0 4px;
}
.color1 {
  color: #999999;
}
.color2 {
  color: #1559ec;
}
.color3 {
  color: #28b266;
}
.color4 {
  color: #ec4832;
}
.color5 {
  color: #999999;
}
.j-scient .form-submit .el-button--primary:hover {
  box-shadow: 0 0 #aaa;
}
.j-scient .el-button--warning:hover {
  box-shadow: 0 0 transparent;
}
.info-policy {
  .en-opeartion {
    span {
      cursor: pointer;
    }
    span:hover {
      color: #4d7aed;
    }
  }
  .el-form-item {
    width: 32%;
    margin-right: 0;
  }
  .line {
    color: #ccc;
    text-align: center;
    padding: 0 4px;
    box-sizing: border-box;
  }
  .form-submit {
    text-align: center;
    margin-bottom: 30px;
    border-bottom: dotted 1px #ccc;
    padding-bottom: 30px;
  }
  .pagination {
    margin-top: 30px;
    width: 100%;
    height: 30px;
    .pagination-num {
      float: left;
      .yellow {
        color: #ff9603;
      }
    }
    .pagination-nav {
      float: right;
    }
  }
}

.icon-no-input-padding /deep/ .el-input--small .el-input__inner {
  padding: 0 10px;
}
.icon-no-input-padding /deep/ .el-input--small .el-input__icon {
  display: none;
}
</style>



