<template>
  <div class="core-indicator-wrap">
    <div class="left">
      <router-link :to="{name:item.name}" v-auth="item.auth" v-for="item in menus" :key="item.name">
        <i :class="['iconfont',item.icon] "></i>{{item.tag}}</router-link>
    </div>
    <div class="right">
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </div>
  </div>
</template>
<script>
import { AUTHS } from '../../assets/js/pageAuth'
export default {
  name: 'information',
  data() {
    return {
      menus: [
        {
          tag: '通知公告管理',
          name: 'infoNotice',
          icon: 'icon-laba',
          auth: AUTHS.NoticeManage
        },
        {
          tag: '科技动态管理',
          name: 'scienceTrend',
          icon: 'icon-keji',
          auth: AUTHS.TechDynamic
        },
        {
          tag: '政策管理',
          name: 'infoPolicy',
          icon: 'icon-zhengce',
          auth: AUTHS.PolicyManage
        }
      ]
    }
  },
  methods: {}
}
</script>
<style lang="scss" scoped>
.core-indicator-wrap {
  display: flex;
  min-height: calc(100vh - 110px - 100px - 21px);
  .left {
    width: 240px;
    float: left;
    background: #f8f8f8;
    padding: 30px;
    box-sizing: border-box;
    border-top-left-radius: 10px;
    a {
      display: block;
      padding: 10px 0;
      font-size: 16px;
      &.router-link-exact-active {
        color: #1559ec;
        font-weight: bolder;
      }
      .iconfont {
        padding-right: 10px;
        color: #2882fe;
      }
    }
  }
  .right {
    border-top-right-radius: 10px;
    float: left;
    width: 1040px;
    padding: 30px;
    box-sizing: border-box;
  }
}
</style>




