<template>
  <div class="info-policy">
    <el-form :inline="true" label-width="80px" ref="filterData" :model="filterData">
      <el-form-item label="政策名称" prop="policyName">
        <el-input placeholder="政策名称" v-model="filterData.policyName" maxlength="100" size="small" style="width: 196px;"></el-input>
      </el-form-item>
      <el-form-item label="状态" prop="status">
        <el-select v-model="filterData.status" placeholder="请选择" size="small" style="width: 196px;">
          <el-option label="全部" value=""></el-option>
          <el-option v-for="item in options" :key="item.id" :label="item.policyStatuName" :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="政策类型" prop="policyTypes">
        <el-select v-model="filterData.policyTypes" placeholder="请选择" size="small" style="width: 196px">
          <el-option label="全部" value=""></el-option>
          <el-option v-for="item in policyTypeDef" :key="item.id" :label="item.policyTypeName" :value="item.policyTypeName">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="发布时间">
        <el-col :span="11">
          <el-form-item prop="releasetimeStart">
            <el-date-picker type="date" v-model="filterData.releasetimeStart" value-format="yyyy-MM-dd HH:mm:ss" @change="getSTime" placeholder="选择日期" style="width: 90px" size="small"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-form-item prop="releasetimeEnd">
            <el-date-picker type="date" v-model="filterData.releasetimeEnd" value-format="yyyy-MM-dd HH:mm:ss" @change="getETime" placeholder="选择日期" style="width: 90px;" size="small"></el-date-picker>
          </el-form-item>
        </el-col>
      </el-form-item>
      <el-form-item label="浏览量">
        <el-col :span="11">
          <el-form-item prop="pageviewStart">
            <el-input placeholder="" type="number" @change="psva" v-model="filterData.pageviewStart" size="small" style="width: 90px;"></el-input>
          </el-form-item>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-form-item prop="pageviewEnd">
            <el-input placeholder="" type="number" @change="pnva" v-model="filterData.pageviewEnd" size="small" style="width: 90px;"></el-input>
          </el-form-item>
        </el-col>
      </el-form-item>
    </el-form>
    <div class="form-submit">
      <el-button type="primary" size="small" round @click="filterQuery" v-auth="AUTH.PolicySelect">查询</el-button>
      <el-button type="primary" size="small" round plain @click="resetForm" v-auth="AUTH.PolicySelect">重置</el-button>
      <el-button type="warning" size="small" round @click="add()" v-auth="AUTH.PolicyInsert">新增</el-button>
    </div>
    <el-table :data="tableData" stripe border style="width: 100%">
      <el-table-column prop="policyName" align="left" label="政策名称" show-overflow-tooltip width="200"></el-table-column>
      <el-table-column prop="policyType" label="政策类型" show-overflow-tooltip width="120"></el-table-column>
      <el-table-column prop="policyLevel" align="center" label="政策级别"></el-table-column>
      <el-table-column align="center" label="状态">
        <template slot-scope="scope">
          <span class="warning" type="text" v-if="scope.row.status === 1">未发布</span>
          <span class="primary" type="text" v-if="scope.row.status === 2">待审核</span>
          <span class="success" type="text" v-if="scope.row.status === 3">已发布</span>
          <span class="warning" type="text" v-if="scope.row.status === 4">未通过</span>
          <span class="warning" type="text" v-if="scope.row.status === 5">已撤回</span>
        </template>
      </el-table-column>
      <el-table-column align="center" width="170" label="发布时间">
        <template slot-scope="scope">
          <span>{{scope.row.releasetime | dateFormat}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="pageview" align="center" label="浏览量" :formatter="pageview"></el-table-column>
      <el-table-column label="操作" align="center" width="230" prop="id">
        <template slot-scope="scope">
          <el-button type="text" v-if="(scope.row.status === 1 || scope.row.status === 4)&&optAuth.updateStatus" @click="updateStatus(scope.row.id,scope.$index,2)">
            申请发布
          </el-button>
          <el-button @click="updateStatus(scope.row.id,scope.$index,2)" type="text" v-if="scope.row.status === 5&&optAuth.updateStatus">
            重新发布
          </el-button>
          <router-link v-if="(scope.row.status === 1 || scope.row.status === 4 || scope.row.status === 5)&&optAuth.update" :to="{name:'infoPolicyEdit',params:{id:scope.row.id}}">
            修改
          </router-link>
          <el-button type="text" v-if="(scope.row.status === 1 ||  scope.row.status === 4 || scope.row.status === 5)&&optAuth.delete" @click="policyDelete(scope.row.id,scope.$index)">
            删除
          </el-button>
          <el-button @click="updateStatus(scope.row.id,scope.$index,3)" type="text" v-if="scope.row.status === 2&&optAuth.updateStatus">
            通过
          </el-button>
          <el-button @click="updateStatus(scope.row.id,scope.$index,4)" type="text" v-if="scope.row.status === 2&&optAuth.updateStatus">
            不通过
          </el-button>
          <el-button @click="updateStatus(scope.row.id,scope.$index,5)" type="text" v-if="scope.row.status === 3&&optAuth.updateStatus">撤回</el-button>
          <router-link :to="{name:'infoPolicyView',params:{id:scope.row.id}}" v-auth="AUTH.PolicyDetail">查看</router-link>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination clearFix">
      <div class="pagination-num">
        共
        <span class="yellow"> {{filterData.total}} </span>条
      </div>
      <div class="pagination-nav">
        <j-paging class="paging" :initPageSize="filterData.pageSize" :initPageIndex="filterData.pageNum" :initTotalCount="filterData.total" @pageChanged="query"></j-paging>
      </div>
    </div>
  </div>
</template>
<script>
import { information as api } from '../../api'
import { jPaging } from '../../components'
import { ButtonAuths } from '../../assets/js/buttonAuth'
import { hasAuth } from '../../assets/js/auth'
export default {
  name: 'infopolicy',
  data() {
    return {
      AUTH: ButtonAuths,
      options: [],
      policyTypeDef: [],
      value: '',
      tableData: [],
      filterData: {
        pageSize: 10,
        pageNum: 1,
        total: null,
        policyTypes: null,
        policyName: '',
        releasetimeStart: '',
        releasetimeEnd: '',
        pageviewStart: null,
        pageviewEnd: null,
        status: null
      }
    }
  },
  computed: {
    optAuth() {
      // 权限依次是:1、不通过、通过、撤回、申请发布、重新发布;2、修改;3、删除;4、查看
      return {
        updateStatus: hasAuth(this.AUTH.NoticeUpdateStatus),
        update: hasAuth(this.AUTH.NoticeUpdate),
        delete: hasAuth(this.AUTH.NoticeDelete),
        detail: hasAuth(this.AUTH.NoticeDetail)
      }
    }
  },
  methods: {
    psva(value) {
      if (value.length > 5) value = value.slice(0, 5)
      if (value < 0) {
        this.filterData.pageviewStart = 0
      } else {
        this.filterData.pageviewStart = Math.floor(value)
      }
    },
    pnva(value) {
      if (value.length > 5) value = value.slice(0, 5)
      if (value < 0) {
        this.filterData.pageviewEnd = 0
      } else {
        this.filterData.pageviewEnd = Math.floor(value)
      }
    },
    getSTime(val) {
      this.filterData.releasetimeStart = val
    },
    getETime(val) {
      this.filterData.releasetimeEnd = val
    },
    getData() {
      let that = this
      api
        .policySelectList(that.filterData)
        .then(res => {
          let jData = res.data
          that.tableData = jData.list
          that.filterData.pageSize = jData.pageSize
          that.filterData.pageNum = jData.pageNum
          that.filterData.total = jData.total
        })
        .catch(err => {
          that.$msgbox({
            title: '系统提示',
            message:
              '<i class="iconfont icon-cuowu"></i><span>' + err.msg + '</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {}
          })
        })
    },
    add() {
      this.$router.push({ name: 'infoPolicyAdd' })
    },
    filterQuery() {
      this.filterData.pageNum = 1
      this.getData()
    },
    query(page) {
      let that = this
      that.filterData.pageNum = page
      that.getData()
    },
    pageview(row, column) {
      if (row.pageview === '') {
        return 0
      } else {
        return row.pageview
      }
    },
    updateStatus(id, index, status) {
      let that = this
      let params = { id: id, status: status }
      api.policyUpdateStatus(params).then(res => {
        that.tableData[index].status = status
      })
    },
    policyDelete(id, index) {
      this.$confirm(
        '<i class="iconfont icon-wenhao"></i><span>删除该数据, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          cancelButtonClass: 'is-plain el-button--primary',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          dangerouslyUseHTMLString: true,
          type: 'success'
        }
      )
        .then(() => {
          let params = { id: id }
          api.policyDelete(params).then(res => {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            // this.tableData.splice(index, 1)
            // this.filterData.total -= 1
            if (this.tableData.length === 1) {
              this.filterData.pageNum -= 1
            }
            this.getData()
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    resetForm() {
      this.filterData.pageNum = 1
      this.$refs['filterData'].resetFields()
      this.getData()
    }
  },
  components: {
    jPaging
  },
  mounted() {
    let that = this
    if (localStorage.filterData) {
      that.filterData = JSON.parse(localStorage.filterData)
    }
    that.getData()
    api
      .policyType('')
      .then(res => {
        that.policyTypeDef = res.data.list
      })
      .catch(res => {
        console.log('没有政策类型查看权限')
      })
    api
      .policyShowStatue('')
      .then(res => {
        that.options = res.data
      })
      .catch(res => {
        console.log('没有状态查看权限')
      })
  },
  beforeRouteLeave(to, from, next) {
    if (to.name === 'infoPolicyEdit' || to.name === 'infoPolicyView') {
      localStorage.filterData = JSON.stringify(this.filterData)
      from.meta.keepAlive = false
    } else {
      localStorage.removeItem('filterData')
      from.meta.keepAlive = false
    }
    next()
  }
}
</script>
<style lang="scss" scoped>
.info-policy {
  .el-form-item {
    width: 32%;
    margin-right: 0;
  }
  .line {
    color: #ccc;
    text-align: center;
    padding: 0 4px;
    box-sizing: border-box;
  }
  .form-submit {
    text-align: center;
    margin-bottom: 30px;
    border-bottom: dotted 1px #ccc;
    padding-bottom: 30px;
  }
  .pagination {
    margin-top: 30px;
    width: 100%;
    height: 30px;
    .pagination-num {
      float: left;
      .yellow {
        color: #ff9603;
        font-weight: bold;
      }
    }
    .pagination-nav {
      float: right;
    }
  }

  & /deep/ .el-table {
    .el-button--text,
    a {
      color: #1559ec;
      text-decoration: underline;
      padding: 0 5px;
    }
    .el-button + .el-button {
      margin-left: 0;
    }
    span {
      &.info {
        color: #9d9d9d;
      }
      &.warning {
        color: #f2aa48;
      }
      &.primary {
        color: #1559ec;
      }
      &.success {
        color: #45bc7a;
      }
    }
  }
  & /deep/ .el-range-editor.el-input__inner {
    padding: 0;
    border: 0;
  }
  & /deep/ .el-range-editor--small .el-range__icon {
    display: none;
  }
  & /deep/ .el-date-editor--datetimerange.el-input__inner {
    width: 220px;
    background: none;
  }
  & /deep/ .el-range-editor--small .el-range-input {
    border: 1px solid #dcdfe6;
    background: #f3f3f3;
    box-sizing: border-box;
    border-radius: 16px;
    padding: 0 2px;
  }
  & /deep/ .el-range-separator {
    color: #ccc;
  }
  & /deep/ .el-range-editor--small .el-range-separator {
    line-height: 32px;
  }
  & /deep/ .el-input--prefix .el-input__inner {
    padding-left: 5px;
  }
  & /deep/ .el-input--suffix .el-input__inner {
    padding-right: 0;
  }
  & /deep/ .el-input__prefix,
  .el-input__suffix {
    display: none;
  }
}
</style>



