<template>
  <div class="info-notice">
	<el-form :inline="true" label-width="80px">
	  <el-form-item label="标题">
	    <el-input placeholder="标题" v-model="queryParams.title" size="small" style="width: 196px;"></el-input>
	  </el-form-item>
	  <el-form-item label="公告状态">
	   <el-select v-model="queryParams.status" placeholder="请选择"  size="small"  style="width: 196px;">
      <el-option label="全部" value=""></el-option>
	    <el-option
	      v-for="item in stateList"
	      :key="item.id"
	      :label="item.policyStatuName"
	      :value="item.id">
	    </el-option>
	  </el-select>
	  </el-form-item>
    <el-form-item label="新增时间" class="icon-no-input-padding">
      <el-col :span="11">
        <el-date-picker
          type="date" placeholder="开始日期"
          style="width: 108px"
          v-model="queryParams.newtimeStart"
          size="small"
          value-format="yyyy-MM-dd HH:mm:ss"
          :picker-options="newtimeSOptions"
        ></el-date-picker>
      </el-col>
      <el-col class="line" :span="2">-</el-col>
      <el-col :span="11">
        <el-date-picker
          type="date"
          placeholder="结束日期"
          style="width: 108px;"
          v-model="queryParams.newtimeEnd"
          size="small"
          value-format="yyyy-MM-dd HH:mm:ss"
          :picker-options="newtimeEOptions"
        ></el-date-picker>
      </el-col>
    </el-form-item>
    <el-form-item label="发布时间" class="icon-no-input-padding">
      <el-col :span="11">
        <el-date-picker
          type="date"
          placeholder="开始日期"
          style="width: 108px"
          v-model="queryParams.releasetimeStart"
          size="small"
          value-format="yyyy-MM-dd HH:mm:ss"
          :picker-options="releaseSOptions"
        ></el-date-picker>
      </el-col>
      <el-col class="line" :span="2">-</el-col>
      <el-col :span="11">
        <el-date-picker
          type="date"
          placeholder="结束日期"
          style="width: 108px;"
          v-model="queryParams.releasetimeEnd"
          size="small"
          value-format="yyyy-MM-dd HH:mm:ss"
          :picker-options="releaseEOptions"
        ></el-date-picker>
      </el-col>
    </el-form-item>
    <el-form-item label="浏览量">
      <el-col :span="11">
        <el-input
          type="number"
          placeholder=""
          v-model="queryParams.pageviewStart"
          size="small"
          min="0"
          style="width: 108px;"
          @change="psva">
        </el-input>
      </el-col>
      <el-col class="line" :span="2">-</el-col>
      <el-col :span="11">
        <el-input
          type="number"
          placeholder=""
          v-model="queryParams.pageviewEnd"
          size="small"
          :min="queryParams.pageviewStart"
          style="width: 108px;"
          @change="pnva">
        </el-input>
      </el-col>
    </el-form-item>
	</el-form>
  <div class="form-submit">
      <el-button type="primary" size="small" round @click="queryList" v-auth="AUTH.NoticeSelect">查询</el-button>
      <el-button type="primary" size="small" round plain @click="clear" v-auth="AUTH.NoticeSelect">重置</el-button>
      <el-button type="warning" size="small" round @click="add()" v-auth="AUTH.NoticeInsert">新增</el-button>
  </div>
  <el-table :data="tableData" stripe border style="width: 100%">
      <el-table-column prop="title" label="标题" show-overflow-tooltip width="220" align="left"></el-table-column>
      <el-table-column prop="status" label="公告状态" show-overflow-tooltip width="100">
        <template slot-scope="scope"><span :class="scope.row.status | statusClass">{{scope.row.status | statusTypeName}}</span></template>
      </el-table-column>
      <el-table-column prop="newtime" align="center" label="新增时间"  width="170" >
        <template slot-scope="scope">{{scope.row.newtime | dateFormat}}</template>
      </el-table-column>
      <el-table-column prop="releasetime" align="center" label="发布时间"  width="170" >
        <template slot-scope="scope">{{scope.row.releasetime | dateFormat}}</template>
      </el-table-column>
      <el-table-column prop="pageview" align="center" label="浏览量"> </el-table-column>
      <el-table-column label="操作" align="center" width="240">
        <template slot-scope="scope">
          <a class="op-a" v-if="scope.row.status === 1&&optAuth.updateStatus" @click="updateStatus(scope.row.id, 2)">申请发布</a>
          <span v-if="scope.row.status === 2&&optAuth.updateStatus">
            <a class="op-a" @click="updateStatus(scope.row.id, 3)">通过</a>
            <a class="op-a" @click="updateStatus(scope.row.id, 4)">不通过</a>
          </span>
          <a class="op-a" v-if="scope.row.status === 3&&optAuth.updateStatus" @click="updateStatus(scope.row.id, 5)">撤回</a>
          <a class="op-a" v-if="scope.row.status === 4&&optAuth.updateStatus" @click="updateStatus(scope.row.id, 2)">申请发布</a>
          <a class="op-a" v-if="scope.row.status === 5&&optAuth.updateStatus" @click="updateStatus(scope.row.id, 2)">重新发布</a>
          <router-link class="op-a" :to="{name:'infoNoticeAdd', query:{id:scope.row.id, isEdit:true}}" v-auth="AUTH.NoticeUpdate">修改</router-link>
          <router-link class="op-a" :to="{name:'infoNoticeAdd', query:{id:scope.row.id, isDetial:true}}" v-auth="AUTH.NoticeDetail">查看</router-link>
          <a class="op-a" @click="deleteNotice(scope.row.id)" v-auth="AUTH.NoticeDelete">删除</a>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination clearFix">
      <div class="pagination-num">共<span class="yellow"> {{queryParams.total}} </span>条
      </div>
      <div class="pagination-nav">
         <j-paging class="paging"
          :initPageSize="queryParams.pageSize"
          :initPageIndex="queryParams.pageNum"
          :initTotalCount="queryParams.total"
          @pageChanged="pageChanged"
         ></j-paging>
      </div>
    </div>
  </div>
</template>
<script>
import { jPaging } from '../../components'
import { informationNotice as api } from '../../api'
import { ButtonAuths } from '../../assets/js/buttonAuth'
import { hasAuth } from '../../assets/js/auth'
export default {
  name: 'infonotice',
  data() {
    return {
      AUTH: ButtonAuths,
      statusClass: 'color1',
      statusFlag: '',
      queryParams: {
        title: '',
        pageSize: 10,
        pageNum: 1,
        total: 0,
        status: '',
        newtimeStart: '',
        newtimeEnd: '',
        releasetimeStart: '',
        releasetimeEnd: '',
        pageviewStart: '',
        pageviewEnd: ''
      },
      stateList: [],
      tableData: [],
      newtimeSOptions: {
        disabledDate: (time) => {
          if (this.queryParams.newtimeEnd !== '') {
            return time.getTime() > new Date(this.queryParams.newtimeEnd)
          } else {
            return time.getTime() > Date.now()
          }
        }
      },
      newtimeEOptions: {
        disabledDate: (time) => {
          if (this.queryParams.newtimeStart !== '') {
            return time.getTime() < new Date(this.queryParams.newtimeStart) || time.getTime() > Date.now()
          } else {
            return time.getTime() > Date.now()
          }
        }
      },
      releaseSOptions: {
        disabledDate: (time) => {
          if (this.queryParams.releasetimeEnd !== '') {
            return time.getTime() > new Date(this.queryParams.releasetimeEnd)
          } else {
            return time.getTime() > Date.now()
          }
        }
      },
      releaseEOptions: {
        disabledDate: (time) => {
          if (this.queryParams.releasetimeStart !== '') {
            return time.getTime() < new Date(this.queryParams.releasetimeStart) || time.getTime() > Date.now()
          } else {
            return time.getTime() > Date.now()
          }
        }
      }
    }
  },
  computed: {
    optAuth() {
      // 权限依次是:1、不通过、通过、撤回、申请发布、重新发布;2、修改;3、删除;4、查看
      return {
        updateStatus: hasAuth(this.AUTH.NoticeUpdateStatus),
        update: hasAuth(this.AUTH.NoticeUpdate),
        delete: hasAuth(this.AUTH.NoticeDelete),
        detail: hasAuth(this.AUTH.NoticeDetail)
      }
    }
  },
  methods: {
    psva(value) {
      if (value.length > 5) value = value.slice(0, 5)
      if (value < 0) {
        this.queryParams.pageviewStart = 0
      } else {
        this.queryParams.pageviewStart = Math.floor(value)
      }
    },
    pnva(value) {
      if (value.length > 5) value = value.slice(0, 5)
      if (value < 0) {
        this.queryParams.pageviewEnd = 0
      } else {
        this.queryParams.pageviewEnd = Math.floor(value)
      }
    },
    pageChanged(value) {
      this.queryParams.pageNum = value
      this.queryList()
    },
    add() {
      this.$router.push({ name: 'infoNoticeAdd' })
    },
    queryList() {
      api.noticeList(this.queryParams).then(res => {
        // console.log(res)
        this.tableData = res.data.list
        this.queryParams.total = res.data.total
      }).catch(err => {
        this.$msgbox({
          title: '系统提示',
          message:
            '<i class="iconfont icon-tanhao"></i><span>' + err.msg + '</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {}
        })
      })
    },
    updateStatus(id, status) {
      console.log(id, status)
      api.noticeUpdateStatus({ id: id, status: status }).then(res => {
        // console.log(res)
        // 通知公告状态修改成功
        // this.$message(
        //   {
        //     message: '通知公告状态修改成功',
        //     type: 'success'
        //   },
        //   1500
        // )
        this.queryList()
      })
    },
    deleteNotice(id) {
      this.$confirm('删除数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        center: true,
        roundButton: true,
        customClass: 'messageBox',
        type: 'success'
      })
        .then(() => {
          api.noticeDelete({ id: id }).then(res => {
            this.$message(
              {
                message: '公告删除成功',
                type: 'success'
              },
              1500
            )
            if (this.tableData.length === 1) {
              this.queryParams.pageNum -= 1
            }
            this.queryList()
          })
        })
        .catch(() => {
          this.$message(
            {
              type: 'info',
              message: '已取消删除'
            },
            1500
          )
        })
    },
    clear() {
      this.queryParams.title = ''
      this.queryParams.status = ''
      this.queryParams.newtimeStart = ''
      this.queryParams.newtimeEnd = ''
      this.queryParams.releasetimeStart = ''
      this.queryParams.releasetimeEnd = ''
      this.queryParams.pageviewStart = ''
      this.queryParams.pageviewEnd = ''
      this.queryList()
    },
    noticeShowStatue() {
      api.noticeShowStatue({}).then(res => {
        this.stateList = res.data
      })
    }
  },
  components: {
    jPaging
  },
  watch: {
    // queryParams(value) {
    //   console.log(value)
    //   if (value.newtimeEnd < value.newtimeStart) {
    //     value.newtimeStart = ''
    //     value.newtimeEnd = ''
    //   }
    //   if (value.releasetimeEnd < value.releasetimeStart) {
    //     value.releasetimeStart = ''
    //     value.releasetimeEnd = ''
    //   }
    //   if (value.pageviewEnd < value.pageviewStart) {
    //     value.releasetimeEnd = value.pageviewStart
    //   }
    // },
    // 'queryParams.pageviewEnd'(newval, oldval) {
    //   console.log(newval)
    //   if (newval < this.queryParams.pageviewStart) {
    //     this.queryParams.releasetimeEnd = this.queryParams.pageviewStart
    //   }
    // }
  },
  mounted() {
    this.noticeShowStatue()
    this.queryList()
  },
  filters: {
    dateFormat2(val) {
      if (!val) {
        return ' '
      }
      let date = new Date(val)
      return (
        date.getFullYear() +
        '-' +
        (date.getMonth() + 1).toString().padStart(2, '0') +
        '-' +
        date
          .getDate()
          .toString()
          .padStart(2, '0')
      )
    },
    statusTypeName(val) {
      if (val === 0) {
        return '全部'
      } else if (val === 1) {
        return '未发布'
      } else if (val === 2) {
        return '待审核'
      } else if (val === 3) {
        return '已发布'
      } else if (val === 4) {
        return '未通过'
      } else if (val === 5) {
        return '已撤回'
      }
    },
    statusClass(val) {
      if (val === 0) {
        return ''
      } else if (val === 1) {
        return 'color1'
      } else if (val === 2) {
        return 'color2'
      } else if (val === 3) {
        return 'color3'
      } else if (val === 4) {
        return 'color4'
      } else if (val === 5) {
        return 'color5'
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.op-a {
  cursor: pointer;
  text-decoration: underline;
  color: #1559ec;
  padding: 0 4px;
}
.color1 {
  color: #999999;
}
.color2 {
  color: #1559ec;
}
.color3 {
  color: #28b266;
}
.color4 {
  color: #ec4832;
}
.color5 {
  color: #999999;
}
.info-notice {
  .el-form-item {
    width: 32%;
    margin-right: 0;
  }
  .line {
    color: #ccc;
    text-align: center;
    padding: 0 4px;
    box-sizing: border-box;
  }
  .form-submit {
    text-align: center;
    margin-bottom: 30px;
    border-bottom: dotted 1px #ccc;
    padding-bottom: 30px;
  }
  .pagination {
    margin-top: 30px;
    width: 100%;
    height: 30px;
    .pagination-num {
      float: left;
      .yellow {
        color: #ff9603;
      }
    }
    .pagination-nav {
      float: right;
    }
  }
}

.icon-no-input-padding /deep/ .el-input--small .el-input__inner {
  padding: 0 12px;
}
.icon-no-input-padding /deep/ .el-input--small .el-input__icon {
  display: none;
}
</style>



