<template>
  <div class="info-policy-add j-theme">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="user-form">
      <el-form-item label="科技动态标题" prop="title">
        <el-input v-model="ruleForm.title" size="small"></el-input>
      </el-form-item>
      <el-form-item label="来源" prop="source">
        <el-input v-model="ruleForm.source" size="small"></el-input>
      </el-form-item>
       <el-form-item label="缩略图">
          <el-upload :action="uploadAction"
            list-type="picture-card"
            :limit = '1'
            :data="uploadData1"
            :before-upload="beforeAvatarUploadImg"
            :file-list="fileListImg"
            :on-remove="delFileImg"
            :on-change="fileListchange"
            :on-success="uploadSuccessImg"
            name="resFile"
            accept=".jpg,.png,.jpeg,.webp">
            <i class="el-icon-plus"></i>
          </el-upload>
      </el-form-item>
      <el-form-item label="科技动态内容">
          <vue-editor v-model="ruleForm.content" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
      </el-form-item>
      <el-form-item label="附件">
             <!-- <el-button size="small" plain>点击上传</el-button>
        <input class="file" name="file" type="file" accept="image/png,image/gif,image/jpeg" @change="update($event)"/> -->
          <el-upload :action="uploadAction"
            :data="uploadData"
            :before-upload="beforeAvatarUpload"
            :file-list="fileList"
            :on-remove="delFile"
            :on-success="uploadSuccess"
             name="resFile"
            :accept="acceptList">
            <el-button size="small" plain :disabled="uploadState">点击上传</el-button>
          </el-upload>
      </el-form-item>
    </el-form>
    <div class="form-submit">
      <el-button type="primary" round size="small" @click="saveForm(1)">保存</el-button>
      <el-button type="warning" round size="small" @click="saveForm(2)">保存并申请发布</el-button>
      <el-button type="primary" round plain size="small" @click="goback">取消</el-button>
    </div>
  </div>

</template>
<script>
import axios from 'axios'
import { VueEditor } from 'vue2-editor'
import http from '../../assets/js/request'
export default {
  data() {
    return {
      uploadState: false,
      uploadData: {
        type: 2,
        filename: ''
      },
      uploadAction: 'http://192.168.200.200:9093/webproxy/res/fileUpload',
      uploadData1: {
        type: 3,
        filename: ''
      },
      acceptList: 'application/pdf,application/vnd.ms-excel,application/msword,application/vnd.ms-works',
      dialogImageUrl: '',
      dialogVisible: false,
      fileListImg: [],
      fileList: [],
      fileName: {
        filename: ''
      },
      ruleForm: {
        title: '',
        source: '',
        content: '',
        status: 1,
        picture: '',
        attachmentName: [],
        attachmentUrl: ''
      },
      rules: {
        title: [
          { required: true, message: '请输入标题', trigger: 'blur' },
          {
            min: 0,
            max: 32,
            message: '长度在 0 到 32 个字符',
            trigger: 'blur'
          }
        ],
        source: [
          { required: true, message: '请输入科技内容', trigger: 'blur' },
          {
            min: 0,
            max: 32,
            message: '长度在 0 到 32 个字符',
            trigger: 'blur'
          }
        ],
        content: [
          { required: true, message: '请输入科技内容', trigger: 'blur' },
          {
            min: 0,
            max: 100,
            message: '长度在 0 到 100 个字符',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  components: {
    VueEditor
  },
  methods: {
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      let that = this
      if (
        file.type !== 'image/png' &&
        file.type !== 'image/jpeg' &&
        file.type !== 'image/bmp'
      ) {
        this.$msgbox({
          title: '系统提示',
          message:
            '<i class="iconfont icon-tanhao"></i><span>请上传图片</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {}
        })
        return false
      }
      if (file.size > 2097152) {
        this.$msgbox({
          title: '系统提示',
          message:
            '<i class="iconfont icon-tanhao"></i><span>上传文件不得大于2M</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {}
        })
        return false
      }
      var formData = new FormData()
      formData.append('resFile', file)
      formData.append('filename', file.name)
      axios({
        url: that.uploadAction,
        method: 'POST',
        data: formData
      })
        .then(result => {
          let url = result.data.data[0].resOriginalUrl
          Editor.insertEmbed(cursorLocation, 'image', url)
          resetUploader()
        })
        .catch(err => {
          console.log(err)
        })
    },
    getData() {
      let params = { id: this.$route.query.id }
      let _this = this
      http
        .post('/technicalTendency/selectDetail', params)
        .then(function(res) {
          _this.ruleForm = res.data
          _this.ruleForm.picture = res.data.picture
          _this.fileListImg.push({
            name: res.data.picture,
            url: res.data.picture
          })
          if (res.data.alist) {
            for (let item of res.data.alist) {
              _this.fileList.push({
                name: item.attachmentName,
                url: item.attachmentUrl
              })
            }
          }
          console.log(_this.fileList)
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    goback() {
      history.go(-1)
    },
    delFile(file, list) {
      this.fileList.splice(list, 1)
    },
    delFileImg(file, list) {
      this.fileListImg.splice(list, 1)
    },
    uploadSuccessImg(res, file, List) {
      let that = this
      let respone = res.data
      that.ruleForm.picture = respone[0].resOriginalUrl
      for (var i = 0; i < respone.length; i++) {
        that.fileListImg.push({
          name: that.uploadData1.filename,
          url: respone[i].resOriginalUrl
        })
      }
    },
    uploadSuccess(res, file, List) {
      let that = this
      let respone = res.data
      for (var i = 0; i < respone.length; i++) {
        that.fileList.push({name: that.fileName.filename, url: respone[i].resOriginalUrl})
      }
      this.uploadState = false
    },
    // update(e) {
    //   let file = e.target.files[0]
    //   let param = new FormData()
    //   param.append('resFile', file)
    //   // console.log(param)
    //   param.append('type', 2)
    //   param.append('filename', file.name)
    //   // console.log('this.axios', axios)

    //   let config = {
    //     headers: { 'Content-Type': 'multipart/form-data' }
    //   } // 添加请求头
    //   axios
    //     .post(
    //       'http://192.168.200.200:9093/webproxy/res/fileUpload',
    //       param,
    //       config
    //     )
    //     .then(response => {
    //       console.log(response.data)
    //     })
    // },
    fileListchange(file, fileList) {
      this.fileListImg = fileList
    },
    handleRemove(file, fileList) {
      this.fileListImg = fileList
    },
    beforeAvatarUpload(file) {
      if (file.type !== 'application/pdf' && file.type !== 'application/vnd.ms-excel' && file.type !== 'application/msword' && file.type !== 'application/vnd.ms-works') {
        this.fileList.unshift({name: this.fileName.filename, url: ''})
        this.$msgbox({
          title: '系统提示',
          message: '<i class="iconfont icon-tanhao"></i><span>请上 pdf,doc,xls,wps文件</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {}
        })
        return false
      }
      if (file.size > 31457280) {
        this.fileList.unshift({name: this.fileName.filename, url: ''})
        this.$msgbox({
          title: '系统提示',
          message: '<i class="iconfont icon-cuowu"></i><span>上传文件不得大于30M</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {}
        })
        return false
      }
      this.fileName.filename = file.name
      this.uploadState = true
    },
    beforeAvatarUploadImg(file) {
      if (this.fileListImg.length > 0) {
        this.$message(
          {
            message: '只能上传一张图片',
            type: 'success'
          },
          1500
        )
      }
      let isLt2M = file.size / 1024 / 1024 < 2
      if (!isLt2M) {
        this.$message.error('上传内容大小不能超过 2MB!')
      }
      this.uploadData1.filename = file.name
      return isLt2M
    },
    save(url, params) {
      http
        .post(url, params)
        .then(function(res) {
          if (res.code === 1) {
            history.go(-1)
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    saveForm(status) {
      console.log(this.ruleForm)
      if (this.ruleForm.title === '') {
        this.$message('请输入标题')
        return
      }
      if (this.ruleForm.content === '') {
        this.$message('请输入科技内容')
        return
      }
      if (status === 1) {
        this.ruleForm.status = status
      }
      if (status === 2) {
        this.ruleForm.status = status
      }
      this.ruleForm.attachmentName = JSON.stringify(this.fileList)
      let params = this.ruleForm
      console.log('params', params)
      if (this.$route.query.id) {
        this.save('/technicalTendency/update', params)
      } else {
        this.save('/technicalTendency/insert', params)
      }
    }
  },
  mounted() {
    // this.baseUrl = window.IS_DEBUG ? 'http://192.168.200.200:9093' : ''
    this.uploadAction = window.api_upload
    if (this.$route.query.id) {
      this.getData()
    }
  }
}
</script>
<style lang="scss" scoped>
.info-policy-add {
  padding: 30px;
  box-sizing: border-box;
  .user-form {
    width: 600px;
    margin: auto;
    .el-input {
      width: 60%;
      margin-right: 5px;
    }
  }
  .form-submit {
    text-align: center;
    margin-top: 80px;
  }
  .ql-snow .ql-toolbar button svg,
  .quillWrapper .ql-snow.ql-toolbar button svg {
    width: 14px;
    height: 14px;
  }
  & /deep/ .ql-editor{
        strong{
          font-weight: bolder;
          em{
            font-style: italic;
            font-weight: bolder;
          }
        }
        em{
          font-style: italic;
        }
      }
}
</style>
<style lang="scss">
@import '~@/assets/css/theme.scss';
.info-policy-add {
  .el-form-item.is-required .el-form-item__label:before {
    content: '';
    color: #f56c6c;
    margin-right: 4px;
  }
  .el-form-item.is-required .el-input:after {
    content: '*';
    color: #f56c6c;
    margin-right: 4px;
    position: absolute;
    right: -20px;
  }
}
</style>



