<template>
  <div class="info-policy-view">
    <el-form :model="policyData" ref="policyData" label-width="100px" class="user-form">
      <el-form-item label="政策名称" prop="policyName">
        <el-input v-model="policyData.policyName" :disabled="true" size="small"></el-input>
      </el-form-item>
      <el-form-item label="政策类型" prop="policyType">
        <el-select  v-model="policyData.policyType" :disabled="true" placeholder="请选择" size="small" style="width:60%">
           <el-option
            v-for="item in policyTypeDef"
            :key="item.id"
            :label="item.policyTypeName"
            :value="item.policyTypeName">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="政策级别" prop="policyLevel">
        <el-select v-model="policyData.policyLevel" :disabled="true" placeholder="请选择" size="small" style="width:60%">
          <el-option
            v-for="item in policyLevelDef"
            :key="item.id"
            :label="item.policyLevelName"
            :value="item.policyLevelName">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="releasetime" label="发布日期" >
          <el-date-picker type="datetime"  v-model="policyData.releasetime" :disabled="true" value-format="yyyy-MM-dd HH:mm:ss"  placeholder="发布日期" size="small"></el-date-picker>
        </el-form-item>
      <el-form-item label="正文" class="bold">
      </el-form-item>
      <el-form-item label="政策依据">
          <vue-editor v-model="policyData.policyAccording" :editorToolbar="customToolbar" :disabled="true"></vue-editor>
      </el-form-item>
      <el-form-item label="政策要点">
          <vue-editor v-model="policyData.policyPointe" :editorToolbar="customToolbar"  :disabled="true"></vue-editor>
      </el-form-item>
      <el-form-item label="适用对象">
          <vue-editor v-model="policyData.suitableUsers"  :editorToolbar="customToolbar" :disabled="true"></vue-editor>
      </el-form-item>
      <el-form-item label="申报条件">
          <vue-editor v-model="policyData.qualification" :editorToolbar="customToolbar"  :disabled="true"></vue-editor>
      </el-form-item>
      <el-form-item label="提交材料">
          <vue-editor v-model="policyData.submission" :editorToolbar="customToolbar"  :disabled="true"></vue-editor>
      </el-form-item>
      <el-form-item label="办理流程">
          <vue-editor v-model="policyData.handlingProcedures" :editorToolbar="customToolbar"  :disabled="true"></vue-editor>
      </el-form-item>
      <el-form-item label="政策附件">
          <el-upload
            action=""
            :disabled="true"
            :limit="10"
            :data="fileName"
            :file-list="fileList">
            <el-button size="small" plain>点击上传</el-button>
          </el-upload>
      </el-form-item>
    </el-form>
    <div class="form-submit">
      <el-button round type="primary" size="small"  v-if="policyData.status === 1 || policyData.status === 4" @click="updateStatus(policyData.id,2)">申请发布</el-button>
      <el-button round type="primary" size="small"  @click="updateStatus(policyData.id,2)" v-if="policyData.status === 5">重新发布</el-button>
      <el-button round type="primary" size="small"   v-if="policyData.status === 1 || policyData.status === 4 || policyData.status === 5"  @click="toEdit">修改</el-button>
      <el-button round type="warning" size="small" v-if="policyData.status === 1 || policyData.status === 4"  @click="policyDelete(policyData.id)">删除</el-button>
      <el-button round type="primary" size="small"  @click="updateStatus(policyData.id,3)" v-if="policyData.status === 2">通过</el-button>
      <el-button round type="warning" size="small"  @click="updateStatus(policyData.id,4)" v-if="policyData.status === 2">不通过</el-button>
      <el-button round type="warning" size="small"  @click="updateStatus(policyData.id,5)" v-if="policyData.status === 3">撤回</el-button>
      <el-button round type="primary" size="small" plain @click="back()">返回</el-button>
    </div>
  </div>

</template>
<script>
  import {information as api} from '../../api'
  import { VueEditor } from 'vue2-editor'
  export default {
    data() {
      return {
        customToolbar: [
          ['link'], ['image', 'code-block']
        ],
        policyData: {},
        fileName: {
          filename: ''
        },
        policyTypeDef: [],
        policyLevelDef: [],
        fileList: []
      }
    },
    components: {
      VueEditor
    },
    methods: {
      back() {
        this.$router.back()
      },
      toEdit() {
        this.$router.replace({name: 'infoPolicyEdit', params: {id: this.policyData.id}})
      },
      updateStatus(id, status) {
        let that = this
        let params = {id: id, status: status}
        api.policyUpdateStatus(params).then(res => {
          if (res.code === 1) {
            that.$msgbox({
              title: '系统提示',
              message: '<i class="iconfont icon-correct"></i><span>' + res.msg + '</span>',
              center: true,
              roundButton: true,
              customClass: 'messageBox',
              type: 'success',
              dangerouslyUseHTMLString: true,
              callback: () => {
                that.back()
              }
            })
          }
        }).catch(err => {
          that.$msgbox({
            title: '系统提示',
            message: '<i class="iconfont icon-cuowu"></i><span>' + err.msg + '</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {}
          })
        })
      },
      policyDelete(id, index) {
        this.$confirm('<i class="iconfont icon-wenhao"></i><span>删除该数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          cancelButtonClass: 'is-plain el-button--primary',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          dangerouslyUseHTMLString: true,
          type: 'success'
        }).then(() => {
          let params = {id: id}
          api.policyDelete(params).then(res => {
            if (res.code === 1) {
              this.$msgbox({
                title: '系统提示',
                message: '<i class="iconfont icon-correct"></i><span>' + res.msg + '</span>',
                center: true,
                roundButton: true,
                customClass: 'messageBox',
                type: 'success',
                dangerouslyUseHTMLString: true,
                callback: () => {
                  this.back()
                }
              })
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '删除失败'
            })
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      }
    },
    mounted() {
      let that = this
      api.policyLevel('').then(res => {
        that.policyLevelDef = res.data.list
      })
      api.policyType('').then(res => {
        that.policyTypeDef = res.data.list
      })
    },
    created() {
      let that = this
      that.policyData.id = that.$route.params.id
      let params = {id: that.policyData.id}
      api.policyDetail(params).then(res => {
        let paList = res.data.paList
        that.policyData = res.data
        that.policyData.releasetime = new Date(res.data.releasetime)
        for (var i = 0; i < paList.length; i++) {
          that.fileList.push({name: paList[i].policyAttachmentsName, url: paList[i].policyAttachmentsUrl})
        }
      }).catch(err => {
        this.$msgbox({
          title: '系统提示',
          message: '<i class="iconfont icon-cuowu"></i><span>' + err.msg + '</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {}
        })
      })
    }
  }
</script>
<style lang="scss" scoped>
  .info-policy-view{
    padding:30px;
    box-sizing:border-box;
    .user-form{
      width:600px;
      margin: auto;
      .el-input{
        width:60%;
        margin-right: 5px;
      }
    }
    .form-submit{
      text-align: center;
      margin-top:80px;
    }
    .ql-snow .ql-toolbar button svg, .quillWrapper .ql-snow.ql-toolbar button svg{
        width:14px;
        height: 14px
      }
      & /deep/ .el-form-item.is-required .el-form-item__label:before{
        content: '';
        color: #f56c6c;
        margin-right: 4px;
      }
      & /deep/ .el-form-item.is-required .el-input:after{
        content: '*';
        color: #f56c6c;
        margin-right: 4px;
        position: absolute;
        right:-20px;
      }
      & /deep/  [disabled], input[disabled] {
       opacity: 1.0; 
       color:#ccc
      }
      & /deep/ .el-input.is-disabled .el-input__inner{
        color:#8a8d94
      }
      & /deep/ .ql-editor{
        strong{
          font-weight: bolder;
          em{
            font-style: italic;
            font-weight: bolder;
          }
        }
        em{
          font-style: italic;
        }
      }
  }
</style>
<style lang="scss">
    .info-policy-view{
      
    }
</style>



